.place-item__modal-content {
  padding: 0;

  .map-container {
    height: 15rem;
    width: 100%;
  }
}

.place-item__modal-actions {
  text-align: right;
}

.place-item {
  margin: 1rem 0;

  .place-item__content {
    padding: 0;

    .place-item__image {
      width: 100%;
      height: 12.5rem;
      margin-right: 1.5rem;

      img {
        width: 100%;
        height: 100%;
        -o-object-fit: cover;
        object-fit: cover;
      }
    }

    .place-item__info {
      padding: 1rem;
      text-align: center;

      h2,
      h3,
      p {
        margin: 0 0 0.5rem 0;
      }
    }

    .place-item__actions {
      padding: 1rem;
      text-align: center;
      border-top: 1px solid #ccc;

      button,
      a {
        margin: 0.5rem;
      }
    }
  }
}

@media (min-width: 768px) {
  .place-item {
    .place-item__content {
      .place-item__image {
        height: 20rem;
      }
    }
  }
}
